import { Injectable } from "@angular/core";
import { Http, URLSearchParams } from "@angular/http";
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';

@Injectable()
export class HttpService {

    constructor(private http: Http) {
    }

    public createAuthHeader(headers: Headers) {
        const token = JSON.parse(localStorage.getItem('social-network-userData')).session.accessToken;
        headers.append('Authentication', token);
    }

    public get(endpoint: string, header?: any, search?: URLSearchParams): Observable<any> {
        return this.makeRequest('GET', endpoint, header, {}, search);
    }

    public post(endpoint: string, data: any, header?: any): Observable<any> {
        return this.makeRequest('POST', endpoint, header, data);
    }

    public put(endpoint: string, data: any, header?: any): Observable<any> {
        return this.makeRequest('PUT', endpoint, header, data);
    }

    private makeRequest(method: string, endpoint: string, headers?: any, data?: any, search?: URLSearchParams) {
        return new Observable((observer) => {
            this.http.request(`${environment.URL}${endpoint}`, {
                method: method,
                headers: headers,
                body: data,
                params: search
            })
                .subscribe({
                    next: (res) => observer.next(res),
                    error: (err) => {
                        console.log(err);
                        observer.error(err)
                    },
                    complete: () => observer.complete()
                })
        })
    }
}


