import { Component } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { HttpService } from "../../services/http.service";
import { Validation } from '../../validation/user-validation';
import { Router } from "@angular/router";

@Component({
    selector: 'register',
    templateUrl: './register.html',
    styleUrls: ['./register.css']
})

export class RegisterComponent {
    registerForm: FormGroup;
    public emailExists: boolean = false;
    public usernameExists: boolean = false;

    constructor(private formBuilder: FormBuilder, private httpService: HttpService, private router: Router,
                private validation: Validation) {
        this.registerForm = this.formBuilder.group(validation.RegisterValidation(), {
            validator: this.validation.matchingPasswords('password', 'confirmPassw')
        });
    }

    public resetError(event): boolean {
        if (event.target.name == 'username') {
            return this.usernameExists = false;
        }
        if (event.target.name == 'email') {
            return this.emailExists = false;
        }
    }

    public onSubmit(): void {
        const user = this.registerForm.value;
        delete user.confirmPassw;

        this.httpService.post('/register', user)
            .subscribe(data => {
                    this.router.navigate(['/login']);
                },
                error => {
                    const user = error.json();

                    if (user.email === this.registerForm.value.email) {
                        this.emailExists = true;
                    }
                    if (user.username === this.registerForm.value.username) {
                        this.usernameExists = true;
                    }
                }
            );
    }
}



