import { FormControl, FormGroup, Validators } from '@angular/forms';

export class Validation {

    private password = new FormControl('', Validators.compose([
        Validators.required,
        Validators.minLength(6),
        Validators.maxLength(255),
        Validators.pattern('^\\S*$')]));
    private confirmPassw = new FormControl('', Validators.compose([
        Validators.required,
        Validators.minLength(6),
        Validators.maxLength(255),
        Validators.pattern('^\\S*$')]));

    private userValidation: any = {
        email: ['', Validators.compose([
            Validators.required,
            Validators.email])],
        username: ['', Validators.compose([
            Validators.required,
            Validators.minLength(3),
            Validators.maxLength(255),
            Validators.pattern('^[a-z0-9-_]*$')])],
    };

    constructor() {
    }

    public matchingPasswords(password: string, confirmPassw: string) {
        return (group: FormGroup): { [key: string]: any } => {
            let original = group.controls[password];
            let confirm = group.controls[confirmPassw];

            if (original.value !== confirm.value) {
                return {
                    mismatchedPasswords: true
                };
            }
        }
    }

    public RegisterValidation(): object {
        const validation = Object.assign({}, this.userValidation);
        validation.password = this.password;
        validation.confirmPassw = this.confirmPassw;

        return validation;
    }
}



