import { Component, EventEmitter, Output, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';
import { FormBuilder, FormGroup } from '@angular/forms';
import { HttpService } from '../../services/http.service';
import { AuthService } from "../../services/auth.service";
import { LoginValidation } from '../../validation/login-validation';

@Component({
    selector: 'login',
    templateUrl: './login.html',
    styleUrls: ['./login.css']
})

export class LoginComponent {
    @ViewChild('loginModal') public loginModal: ModalDirective;
    @Output() public onFinish: EventEmitter<boolean> = new EventEmitter();
    @Output() public onHidden: EventEmitter<ModalDirective> = new EventEmitter<ModalDirective>();

    loginForm: FormGroup;
    public loginError: boolean = false;

    constructor(private formBuilder: FormBuilder, private httpService: HttpService, private auth: AuthService) {
        this.loginForm = this.formBuilder.group(LoginValidation());
    }

    public resetError(): boolean {
        return this.loginError = false;
    }

    public onSubmit(): void {
        const user = this.loginForm.value;

        this.httpService.post('/login', user)
            .subscribe(data => {
                    this.auth.Login(data._body, (result) => {});
                },
                error => this.loginError = true);
    }
}


